<style>.notification-top-left,
.notification-top-center,
.notification-top-right,
.notification-bottom-left,
.notification-bottom-center,
.notification-bottom-right {
    position: fixed;
    width: 270px;
}

.notification-top-left {
    top: 0;
    left: 0;
}

.notification-top-center {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.notification-top-right {
    top: 0;
    right: 0;
}

.notification-bottom-left {
    bottom: 0;
    left: 0;
}

.notification-bottom-center {
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.notification-bottom-right {
    bottom: 0;
    right: 0;
}

.notification {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    margin: 12px;
    background: #fff;
    color: #000;
    border-radius: 6px;
}

.notification-context {
    width: 210px;
    padding: 12px 6px 12px 12px;
    box-sizing: border-box;
    word-wrap: break-word;
}

button {
    display: block;
    width: 40px;
    padding: 0 0 2px;
    margin: 0;
    border: none;
    border-left: 1px solid #eee;
    outline: none;
    background: none;
    cursor: pointer;
    font-size: 20px;
    color: #000;
    box-sizing: border-box;
}

button:hover {
    background: rgba(0, 0, 0, 0.01);
}

@-webkit-keyframes test {
  from{
    transform: translateX(100px);
  }
}

@keyframes test {
  from{
    transform: translateX(100px);
  }
}
</style>
<script>
  import Notification from './Notification.svelte';

  import { setContext } from 'svelte';

  import context from '../context';
  import store from '../store';

  export let notificationItem = null;

  setContext(context, store);

  const positions = [
    'top-left',
    'top-center',
    'top-right',
    'bottom-left',
    'bottom-center',
    'bottom-right',
  ];

  const getItem = () => notificationItem();
</script>

<slot></slot>
<div class="notifications">
  {#each positions as position}
    <div class={`notification-${position}`}>
      {#each $store as notification (notification.id)}
        {#if notification.position === position}
          {#if notificationItem}
            <svelte:component this={notificationItem} {notification}/>
          {:else}
            <Notification {notification}/>
          {/if}
        {/if}
      {/each}
    </div>
  {/each}
</div>
